:root {
  --font-body: "Sofia Pro", sans-serif;
}

@font-face {
  font-family: "Galano Grotesque";
  src: url("fonts/GalanoGrotesque.ttf") format("truetype");
}
:root {
  --background: #10121c;
  --primary: #FF7F01;
  --secondary: #ff9100;
  --gradiant-end: #FF7F01;
  --gradiant-start: #ff9100;
  --button-dark: #8694e8;
}

html {
  font-size: 100%;
  box-sizing: border-box;
  font-family: var(--font-body);
  width: 100%;
  overflow-x: hidden;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  width: 100%;
  overflow: hidden !important;
  background-color: var(--background);
}

a,
a:visited,
a:focus,
a:active {
  text-decoration: none;
}

link {
  text-decoration: none !important;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 8.75rem;
  max-width: 69.375rem;
  margin: 0 auto;
  position: relative;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}
@media (max-width: 65em) {
  .container {
    padding: 10px;
  }
}

.hero-section {
  height: 90vh;
}
@media (max-width: 65em) {
  .hero-section {
    height: 80vh;
  }
}

.why-us-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 100px 0;
}
@media (max-width: 65em) {
  .why-us-section {
    width: 100%;
    padding: 0;
  }
}
.why-us-section__underlay {
  position: absolute;
  z-index: -50;
  top: -50px;
  width: 100vw;
}
.why-us-section__underlay img {
  width: 100%;
}
.why-us-section__text {
  width: 60%;
  text-align: center;
}
@media (max-width: 65em) {
  .why-us-section__text {
    width: auto;
  }
}
.why-us-section__text--subtitle {
  margin-top: 20px;
  color: #8494e2;
  font-weight: 300;
  font-size: 28px;
}
.why-us-section__text--title {
  color: #ff7f01;
  font-size: 48px;
  font-weight: 600;
  text-align: center;
  display: flex;
  justify-content: center;
  position: relative;
  width: 300px;
  margin: auto;
}
.why-us-section__text--title .img-container {
  height: 70px;
  width: 140px;
  position: absolute;
  left: 130px;
  background: #10121c;
  display: flex;
  align-items: center;
}
.why-us-section__text--title .img-container img {
  height: 70%;
  transform: rotate(-90deg);
}
.why-us-section__text--title span {
  color: white;
  font-family: "Galano Grotesque";
  font-weight: bold;
  font-size: 52px;
  margin-left: 10px;
}
.why-us-section__text--text {
  color: white;
  font-weight: 400;
  opacity: 0.8;
}
.why-us-section__cardscontainer {
  display: flex;
  justify-content: center;
  align-items: center;
}
.why-us-section__cards {
  display: grid;
  width: 100%;
  grid-template-columns: 320px 320px 320px;
}
@media (max-width: 65em) {
  .why-us-section__cards {
    grid-template-columns: 1fr;
  }
}
.why-us-section__cards--col {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.why-us-section__card {
  background-color: rgba(42, 46, 67, 0.7);
  width: 280px;
  padding: 15px;
  border-radius: 20px;
  margin-bottom: 30px;
  z-index: 10;
}
.why-us-section__card--icon {
  display: flex;
  justify-content: center;
  align-items: center;
  aspect-ratio: 1;
  border-radius: 15px;
  padding: 20px;
  width: 50px;
  background: red;
}
.why-us-section__card--icon img {
  transform: scale(1.2);
}
.why-us-section__card--title {
  color: white;
  margin-top: 15px;
  font-weight: 500;
  font-size: 18px;
}
.why-us-section__card--text {
  font-size: 12px;
  font-weight: 300px;
  color: white;
  opacity: 0.7;
  margin-bottom: 50px;
}

.nav {
  display: flex;
  align-items: center;
  color: white;
  padding: 20px 0;
  justify-content: space-between;
  font-size: 20px;
}
@media (max-width: 65em) {
  .nav {
    display: none;
  }
}
.nav__left {
  display: flex;
  align-items: center;
}
.nav__logo {
  display: flex;
  align-items: center;
  cursor: pointer;
  margin-right: 170px;
}
.nav__logo--img {
  height: 30px;
  margin-right: 10px;
}
.nav__items {
  display: flex;
  align-items: center;
  list-style: none;
}
.nav__item {
  margin-right: 50px;
  cursor: pointer;
}
.nav__item:hover {
  color: var(--secondary);
}
.nav__item--active {
  color: var(--secondary);
}
.nav__right {
  display: flex;
  align-items: center;
}

.button {
  padding: 10px 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 35px;
  border-radius: 3px;
  color: white;
}
@media (max-width: 65em) {
  .button {
    margin: 10px auto;
    width: 100%;
  }
}
.button--primary {
  background: linear-gradient(to left, var(--gradiant-start), var(--gradiant-end));
}
.button--secondary {
  background-color: var(--button-dark);
}

.main {
  width: 40%;
  margin-top: 50px;
}
@media (max-width: 65em) {
  .main {
    width: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: auto;
  }
}
.main__title {
  color: white;
}
.main__title--small {
  font-size: 28px;
  color: var(--primary);
  margin-bottom: 5px;
}
.main__title--main {
  color: white;
  font-size: 46px;
  font-weight: 500;
  margin-bottom: 2px;
}
.main__title--subtitle {
  color: white;
  font-weight: 400;
  margin-bottom: 25px;
}
.main__text {
  font-size: 20px;
  color: white;
  font-weight: 300;
  margin-bottom: 40px;
}
.main__buttons {
  display: flex;
  justify-content: space-around;
}
@media (max-width: 65em) {
  .main__buttons {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

.phone__img {
  position: absolute;
  z-index: 10;
  width: 70%;
  top: 120px;
  right: 10px;
}
@media (max-width: 65em) {
  .phone__img {
    display: none;
  }
}
.phone__img img {
  width: 100%;
}

.upgrade-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 50px;
  margin-bottom: 150px;
  position: relative;
}
@media (max-width: 65em) {
  .upgrade-section {
    margin-top: 150px;
  }
}
.upgrade-section__underlay {
  position: absolute;
}
.upgrade-section__dots1 {
  position: absolute;
  top: -150px;
  left: 10px;
  transform: scale(1.1);
}
.upgrade-section__dots2 {
  position: absolute;
  bottom: -150px;
  right: 10px;
  transform: scale(1.1);
}
.upgrade-section__card {
  position: relative;
  width: 80%;
  padding: 30px 50px;
  border-radius: 10px;
  background: #313457;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
@media (max-width: 65em) {
  .upgrade-section__card {
    justify-content: center;
    align-items: center;
    padding: 60px 30px;
  }
}
.upgrade-section__card--underlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}
.upgrade-section__card--underlay img {
  width: 100%;
}
.upgrade-section__card--dots {
  position: absolute;
  right: 10px;
  bottom: -25px;
  transform: scale(0.7);
}
.upgrade-section__card--coin1 {
  position: absolute;
  transform: scale(0.7);
  top: -180px;
  right: 10px;
}
@media (max-width: 65em) {
  .upgrade-section__card--coin1 {
    transform: scale(0.5) translateX(100%);
    top: -140px;
    right: 50%;
  }
}
.upgrade-section__card--coin2 {
  position: absolute;
  transform: scale(0.7);
  top: 10px;
  right: 150px;
}
@media (max-width: 65em) {
  .upgrade-section__card--coin2 {
    display: none;
  }
}
.upgrade-section__card--coin3 {
  position: absolute;
  transform: scale(0.7);
  bottom: 40px;
  right: 60px;
}
@media (max-width: 65em) {
  .upgrade-section__card--coin3 {
    display: none;
  }
}
.upgrade-section__card--title {
  color: white;
  font-size: 38px;
  width: 30%;
  color: #ff7f01;
}
@media (max-width: 65em) {
  .upgrade-section__card--title {
    width: 100%;
  }
}
.upgrade-section__card--title span {
  color: white;
  font-size: 42px;
  font-weight: 700;
  font-family: "Galano Grotesque";
}
.upgrade-section__buttons {
  display: flex;
  justify-content: flex-start;
}
.upgrade-section__button {
  margin-top: 20px;
  padding: 10px 35px;
  background: #ff8e00;
  border-radius: 4px;
  color: white;
}

.footer {
  background: white;
  width: 100vw;
  display: grid;
  grid-template-columns: 4fr 3fr 3fr;
  padding: 50px 50px 200px 50px;
  z-index: 100;
  position: relative;
  color: #1c1e2c;
}
@media (max-width: 65em) {
  .footer {
    grid-template-columns: 1fr;
  }
}
@media (max-width: 65em) {
  .footer__left {
    display: flex;
    flex-direction: column;
    margin-bottom: 50px;
  }
}
.footer__logo {
  width: 30%;
}
@media (max-width: 65em) {
  .footer__logo {
    width: 90%;
  }
}
.footer__logo img {
  width: 100%;
}
.footer__text {
  margin-left: 70px;
  font-weight: 300;
  opacity: 0.9;
}
@media (max-width: 65em) {
  .footer__text {
    margin-left: 0;
  }
}
.footer__center {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.footer__center--header {
  font-size: 24px;
  font-weight: 500;
  height: 68px;
}
@media (max-width: 65em) {
  .footer__center--header {
    height: auto;
    margin-bottom: 20px;
    text-align: center;
  }
}
.footer__center--links {
  opacity: 0.9;
  list-style: none;
  padding-left: 0;
  margin: 0;
}
.footer__center--link {
  margin-bottom: 10px;
  list-style: none;
  margin-left: 0;
  font-size: 16px;
  font-weight: 300;
}
@media (max-width: 65em) {
  .footer__center--link {
    text-align: center;
  }
}
.footer__center--link a {
  color: #1c1e2c;
}